/* fonts */

.DaVinci {
  position: relative;

  display: inline-block;
  margin-left: 1rem;
  margin-right: 1rem;

  font-family: @daVinci;
  font-size: 4.4rem;
  // line-height: 77px;
  letter-spacing: -0.02em;
  text-transform: initial;
  color: @gold;

  transform: rotate(-5.91deg);
}

.blaster {
  display: inline-block;
  font-family: @blaster;
  font-style: italic;
  &--green {
    color: @emerald;
  }
}

/* buttons */

.btn-submit {
  position: relative;

  display: flex;
  justify-content: center;
  align-items: center;

  width: 3.7rem;
  height: 3.7rem;

  border-radius: 50%;
  background-color: @white;

  transition: @s;

  &__icon {
    fill: @dark;
  }

  &:hover,
  &:focus {
    background-color: @white-hover;
  }

  &::before {
    content: '';
    position: absolute;
    left: -0.1rem;

    width: 0.7rem;
    height: 0.7rem;

    border-radius: 50%;

    background-color: @emerald;
  }
}

.btn-modal {
  box-sizing: border-box;
  position: relative;

  display: flex;
  justify-content: center;
  align-items: center;

  width: 25.5rem;
  height: 5.7rem;

  font-weight: 700;
  font-size: 1.7rem;
  text-align: center;

  border-radius: 1rem;

  transition: @s;

  &__decor-line {
    position: absolute;
    bottom: -0.2rem;
    left: 50%;
    transform: translateX(-50%);

    width: 8.8rem;
    height: 0.5rem;

    background-color: @emerald;

    transition: inherit;

    &::before,
    &::after {
      content: '';
      position: absolute;
      bottom: 0;

      height: 0;
      width: 0;
      border-top: 0.5rem solid transparent;
    }

    &::before {
      left: -0.3rem;
      border-right: 0.3rem solid @emerald;
    }

    &::after {
      right: -0.3rem;
      border-left: 0.3rem solid @emerald;
    }
  }

  &:hover,
  &:focus {
    outline: none;
    .btn-modal__decor-line {
      width: 55%;
    }
  }

  &--dark {
    color: #fff;
    background-color: @btn-dark-bg;
    border: 0.2rem solid @btn-dark-border;
    box-shadow: 10px 10px 40px rgba(62, 53, 75, 0.29);
    &:hover,
    &:focus {
      background-color: @brown;
    }
  }
  &--white {
    color: @dark;
    background-color: @btn-white-bg;
    border: 0.2rem solid @btn-white-border;
    // box-shadow: #C8D9EC;
    box-shadow: 10px 10px 40px #C8D9EC;
    &:hover,
    &:focus {
      box-shadow: none;
      background-color: @btn-white-border;
      // background-color: #fff;
    }
  }
  &--longer {
    width: 30.5rem;
    .btn-modal__decor-line {
      width: 13.0rem;
    }
  }
}

.btn-link {
  display: flex;
  justify-content: center;
  align-items: center;

  width: 7rem;
  height: 8.2rem;

  background-color: #FFFFFF;
  box-shadow: 8px 8px 15px #D5E1F3;
  border-radius: 9rem;

  transition: @s;

  &__icon {
    fill: @dark;
    transition: inherit;
  }

  &:hover,
  &:focus {
    outline: none;
    box-shadow: none;
    .btn-link__icon {
      fill: @icon-dark-hover;
    }
  }
}

.btn-region-1 {
  position: relative;
  display: flex;
  align-items: center;

  color: inherit;

  padding-left: 2rem;

  transition: @s;

  &::before {
    position: absolute;
    content: '';
    top: -0.1rem;
    left: -0.2rem;

    width: 1.8rem;
    height: 2.3rem;

    // background-image: url('../img/svg/region-1-w.svg');
    background-image: url('../img/svg/region-1-w.svg');
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
  }

  &__icon {
    margin-right: 0.4rem;
    transition: inherit;
  }

  &:hover,
  &:focus {
    outline: none;
    opacity: 0.7;
