  //       color: inherit;
  //     }
  //   }
  // }
}

.btn-region-2 {
  position: relative;
  display: flex;
  align-self: flex-start;

  // width: 304px;
  width: 27.3rem;

  font-weight: 700;
  font-size: 1.7rem;

  transition: @s;

  &__icon {
    position: absolute;
    left: -3rem;

    color: #CACACA;
    transition: inherit;

    // margin-right: 4px;
  }

  &::before {
    content: '';
    position: absolute;
    // left: 33px;
    // bottom: 9px;
    bottom: -0.6rem;

    // width: 270px;
    width: 100%;
    height: 0.3rem;

    background-color: @red;
    border-radius: 0.9rem;

    transition: inherit;
  }

  &:hover,
  &:focus {
    outline: none;
    // opacity: 0.7;
    .btn-region-2__icon {
      color: rgba(0,0,0,1);
    }
  }
}

.btn-compare {
  position: relative;

  display: flex;
  align-items: center;

  width: 14.8rem;
  height: 2.9rem;
  // padding-left: 20px;
  padding-left: 2.7rem;

  font-weight: 700;
  font-size: 1.6rem;
  color: #fff;

  background-color: rgba(35, 46, 62, 0.64);
  border-radius: 9rem;

  transition: @s;

  &__icon {
    position: absolute;
    top: 0.4rem;
    left: 0.7rem;
    fill: #fff;

    transition: inherit;
  }

  &:hover,
  &:focus {
    outline: none;
    color: @icon-white-hover;
    .btn-compare__icon {
      fill: @icon-white-hover;
    }
  }

  &__desc {
    position: absolute;
    transition: @s;
  }

  &__selected {
    // display: none;
    // position: absolute;
    // top: 0;
    opacity: 0;
    visibility: hidden;
  }

  &.selected {
    .btn-compare__selected {
      opacity: 1;
      visibility: visible;
    }
    .btn-compare__not-selected {
      opacity: 0;
      visibility: hidden;
    }
  }
}

