  display: flex;
}

.header__logo {
  margin-left: 2rem;
}

.header__rows-wrap {
  display: flex;
  flex-direction: column;
  // .b;

  width: 117.8rem;
  margin-left: auto;
  margin-right: 1.9rem;
  padding-top: 1.2rem;
}

.header__row-top {
  display: flex;
  align-items: center;
  margin-bottom: 3rem;
}

