.reviews-swiper {
  padding-top: 8.3rem;
  padding-bottom: 8.3rem;
}

.reviews-swiper__main-text-wrap {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.reviews-swiper__main-title {}

.reviews-swiper__count-reviews {}

.reviews-swiper__big-container {
  .swiper-container {
    // margin-left: 5px;
    // margin-right: 0px;
  }
}

.reviews-swiper__swiper-container {
  padding-top: 7rem;
}

.reviews-swiper__blockquote {
  box-sizing: border-box;
  position: relative;

  width: 65.7rem;
  // height: 305px;
  margin: 0;
  padding-top: 4rem;
  padding-left: 3.6rem;
  padding-right: 3.6rem;
  padding-bottom: 4rem;

  background-color: #FFF;
  border-radius: 7rem;
  box-shadow: 0px 20px 30px #D4E2F1;

  transition: @s;

  &:hover {
    box-shadow: none;
  }
}

.reviews-swiper__link {
  &:hover,
  &:focus {
    .btn-link__icon {
      fill: @icon-dark-hover;
    }
  }
}

.reviews-swiper__author-block {
  margin-bottom: 2.7rem;
  padding-left: 15.5rem;
  font-weight: 700;
}

.reviews-swiper__author-photo-wrap {
  position: absolute;
  top: -4.6rem;
  left: 3.2rem;

  width: 14.4rem;
  height: 14.4rem;
}

.reviews-swiper__author-photo {
  .img-cover;
  border-radius: 50%;
  box-shadow: 0px 20px 30px #D4E2F1;
}

.reviews-swiper__author-name {
  display: inline-block;
  margin-bottom: 0.8rem;
  font-style: normal;
  font-size: 2.5rem;
}

.reviews-swiper__author-theme {
  font-size: 1.8rem;
  color: @light-gray;
}

.reviews-swiper__btn-link {
  box-shadow: none;
  position: absolute;
  top: 3.4rem;
  right: 1.8rem;
}

.reviews-swiper__author-text {
  line-height: 2.7rem;
}

.reviews-swiper__arrows-wrap {
}

.reviews-swiper__button-prev {
}

.reviews-swiper__button-next {
}

.reviews-swiper__swiper-pagination {
}
