.rehab-swiper__item {
  box-sizing: border-box;

  overflow: hidden;
  position: relative;

  width: 30.5rem;
  // width: 100%;
  height: 50.5rem;

  background-image: linear-gradient(180deg, #FFFFFF 0%, #E6F1FD 100%);
  border-radius: 7rem;

  transition: @s;

  &.tab-focus,
  &:hover,
  &:focus {
    box-shadow: 0px 30px 50px #B7C3DF;
  }
}

