.rehab-swiper__img-wrap {
  position: absolute;
  bottom: 0;
  z-index: 0;

  display: flex;
  width: 100%;
  height: 39.8rem;

  /* свг-маска */
  clip-path: url(../#clip-small-top);
  // clip-path: url(../img/sprite.svg#clip-small-top);
  // filter: url(../#filter-small-top);
  filter: url(../img/sprite.svg#filter-small-top);

  &::before,
  &::after {
    content: '';
    position: absolute;
    width: 100%;
  }

  &::before {
    bottom: 0;
    left: 0;

    // height: 32.8rem;
    // height: 100%;
    height: 37.8rem;

    border-radius: 7rem 0 7rem 7rem;
    box-shadow: inset 20px 40px 60px rgba(0, 0, 0, 0.25);

    // @media (max-width: 96em) {
    //   height: 33.1rem;
    // }
  }

