.services {
  // padding-top: 11.2rem;
  padding-top: 7.5rem;
  // padding-bottom: 15rem;
  padding-bottom: 7.5rem;
}

.services__list {
  display: flex;
  justify-content: space-between;
}

.services__item {
  width: 30rem;
  text-align: center;
}

.services__link {
  display: flex;
  flex-direction: column;
  align-items: center;

  &:hover,
  &:focus {
    .services__img-wrap {
      box-shadow: 15px 15px 80px #ADB9D9;
    }
    .services__btn-link {
      box-shadow: none;
      .btn-link__icon {
        fill: @icon-dark-hover;
      }
    }
  }
}

.services__item-title {
  margin-bottom: 1.4rem;
  .padding-inline(4rem);

  font-weight: 700;
  font-size: 3rem;
  line-height: 3.5rem;
  color: @dark;
}

.services__desc {
}

.services__img-wrap {
  width: 100%;
  height: 39.7rem;
  margin-top: 2.3rem;
  margin-bottom: 4.9rem;

  border-radius: 9rem;
  box-shadow: 15px 45px 80px #ADB9D9;

  transition: @s;
}

.sesrvices__img {
  .img-cover;
}

.services__subtitle {
  margin-bottom: 0.8rem;
  font-weight: 700;
  font-size: 2.2rem;
  // line-height: 32px;
}

.services__btn-link {
  margin-top: 2rem;
}
